import React, { Component } from "react";
import axios from '../../../routes/axios-orders';
import Button from '../../../components/UI/Button/Button';
import Spinner from '../../../components/UI/Spinner/Spinner';

import styles from './ContactData.module.css';

class ContactData extends Component {
	state = {
		name: '',
		email: '',
		address: {
			'street': '',
			'postalCode': ''
		},
		loading: false
	}
	orderHandler = (event) => {
		event.preventDefault();
					this.setState( { loading: true } );
			const order = {
			    ingredients: this.props.ingredients,
			    price: this.props.price,
			    customer: {
			        name: 'Bronson Quick',
			        address: {
			            street: 'Here St',
			            zipCode: '4107',
			            country: 'Australia'
			        },
			        email: 'bronson@bronsonquick.com.au'
			    },
			    deliveryMethod: 'fastest'
			}
			axios.post( '/orders.json', order )
			    .then( response => {
					this.setState( { loading: false } );
					this.props.history.push('/');
			    } )
			    .catch( error => {
			        this.setState( { loading: false } );
			    } );
	}
	render() {
		let form = (
			<form>
					<input  className={styles.Input} type="text" name="name" placeholder="Your name" />
					<input  className={styles.Input} type="text" name="email" placeholder="Your email" />
					<input  className={styles.Input} type="text" name="street" placeholder="Your street" />
					<input  className={styles.Input} type="text" name="postalCode" placeholder="Postal Code" />
					<Button btnType="Success" clicked={this.orderHandler}>Order</Button>
				</form>
		);
		if (this.state.loading) {
			form = <Spinner />;
		}
		return(
			<div className={styles.ContactData} >
				<h4>Enter your contact data</h4>
				{form}
			</div>
		);
	}
}

export default ContactData;
